.m_container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  user-select: none;
}

.m_container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 50%;
  left: 20%;
  height: 23px;
  width: 23px;
  border: 1px solid #eee;
  border-radius: 20%;
  transform: translateY(-30%);
}

/* When the checkbox is checked, add a blue background */
.m_container input:checked ~ .checkmark {
  background-color: #40A137;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.m_container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.m_container .checkmark:after {
  left: 8px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

