.layout_wrapper_settings{
    padding: 25px 20px 0 20px;
    display: grid;
    grid-template-areas: "header header"
                         "sidebar content ";
    grid-template-columns: 20vw 1fr;
    grid-template-rows: 10vh auto;
    grid-gap: 20px;

    .wrapper_settings_navbar{
        grid-area: sidebar;
        border-right: 1px solid #e6e6e6;
        padding-right: 2rem;
        h1 {
            color: #3ABE2F;
            font-size: 1.5vw;
        }

        .settings_navbar_list{
            @include displayFlex(column, auto, flex-start);
            gap: 20px;
            
            margin-top: 4rem;

            .navbar_list_item {
                @include displayFlex(row, flex-start, center);
                gap: 10px;
                cursor: pointer;
                width: 100%;

                &:last-child{
                    .list_item_title{

                        margin-left: 5px;
                    }
                }
                .list_item_title{
                    margin-left: 10px;
                    margin-top: 1rem;

                    .item_title_title{
                        font-size: 1.2vw;
                    }
                    .item_title_shortList {
                        font-size: .8vw;
                        margin-top: 1rem;
                    }
                }
                .list_item_iconChevron {
                    margin-left: auto;
                }
            }
        }

    }
    .wrapper_setting_content{
        grid-area: content;

        .setting_content__heading{
            .heading_title{
                font-size: 1.5vw;
                color: #000;
                margin-bottom: 1rem;
            }

            border-bottom: 0.5px solid #e6e6e6;
        }

        .setting_content__body{
            .body_title{
                font-size: 1.2vw;
                color: #000;
                font-weight: 200;
                margin-top: 1.4rem;
                margin-bottom: 1rem;
            }
        }
    }
}